
import React from "react";

export const  Reward=props=>
<div className="panel panel-default reward-item">
  <div className="panel-body">
    <div className="row">
      <div className="col-sm-6">
        <span className="glyphicon glyphicon-star"></span> 
        {props.title}
      </div>
      <div className="col-sm-3">
        <span className="label label-default"> {props.points} </span>
     </div>
     <div className="col-sm-3">
       <input onClick={props.handleDeleteReward} type="button" id="deleteRewards" value="Delete" />
        
     </div>  
    </div>
  </div>
</div>;






